.Navbar {
  display: block;
}

.Navbar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;

  height: 100%;
}

.Navbar__brand {
  margin-top: 4rem;
}

.Logo {
  -webkit-animation: Logo-spin infinite 20s linear;
          animation: Logo-spin infinite 20s linear;
  height: 80px;
}

.Navbar__list {
  display: -ms-flexbox;
  display: flex;
  list-style: none;
}

.Navbar .Navbar__list a {
  display: block;
  -ms-flex: 0 0 auto;
      flex: 0 0 auto;
  
  position: relative;
  padding: 5.6rem 1.6rem 1.6rem;
  max-width: 60rem;

  background: #f4f6f8;
  color: #1a222b;
  text-decoration: none;

  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: .1rem;
  line-height: 1;
}

.Navbar .Navbar__list li {
  display: table-cell;
}

@-webkit-keyframes Logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes Logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


